.modalWr {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(2px);
  }

.modalInner {
    position: relative;
    padding: 3rem;
    background-color: white;
    border-radius: 8px;
    max-width: 480px;
    min-width: 320px;
    max-height: 670px;
}   

.closeBtn {
    position: absolute;
    right: 10px;
    top: 10px;
}